/* About.css */

.homescreen {
    display: flex;
    min-height: 100vh;
    min-width: 100vw;
    align-items: center;
    justify-content: center;
    padding: 24px;
    background-color: #f7fafc; /* equivalent to bg-gray-100 */
  }
  
  .medhome {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
  }
  
  .title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .description {
    font-size: 1.125rem;
    line-height: 1.75rem;
    font-family: 'serif'; /* equivalent to font-serif */
    margin-top: 20px;
  }
  
  .profile-image {
    width: 150px;
    height: auto;
    border-radius: 1rem; /* equivalent to rounded-xl */
    margin-top: 20px;
  }
  
  .button-container {
    display: flex;
    flex-direction: row;
    gap: 24px;
    margin-top: 40px;
    justify-content: center;
    width: 100%;
  }
  
  .button {
    background-color: #2d3748; /* equivalent to bg-gray-800 */
    color: white;
    padding: 12px 24px;
    border-radius: 0.5rem;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .button:hover {
    background-color: #4a5568; /* equivalent to hover:bg-gray-700 */
  }
  